import icon from "../assets/image/icon.png";

class Header {
  /**
   * Headerprops
   * @param {Object} logo logo
   * @param {Array<Object>} functionModules 
   * @param {String} extra 
   */
  constructor(logo, functionModules, extra = "") {
    this.logo = logo;
    this.functionModules = functionModules;
    this.extra = extra;
  }

  render() {
    return `
    <div id="header">
        ${this.renderLogo()}
        ${this.renderNav()}
    </div>`;
  }

  renderLogo() {
    const info = this.logo;
    return `
    <a href="/home">
      <img id="header-logo" src=${icon} style="width:${info.width}px;height:${info.height}px"></img>
    </a>`;
  }

  renderNav() {
    const content = this.functionModules.map((item) => {
      return `
            <div>
                <a href=${item.url}>${item.name}</a>
                <div>
                </div>
            </div>
        `;
    });
    return `${content.join("")}`;
  }
}

export default Header;
